/* lightbox */


/* Lightbox Overlay */
.lightbox-overlay {
  position: fixed;
  top: 0; left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.8); /* This is the overlay color */
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 999;
}


/* Content Wrapper */
.lightbox-wrapper {
  background: white;
  padding: 1rem;
  border-radius: 12px;
  top: 1px;
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.15);
  max-width: 500px;   /* fixed or max width */
  width: 400px;       /* set a width */
  animation: fadeIn 0.3s ease-out;
  position: absolute; /* important for positioning */
  z-index: 1000;
}
.lightbox-wrapper::after {
  content: '';
  position: absolute;
  top: 20px; /* vertically aligned with text */
  right: -10px;
  border-top: 10px solid transparent;
  border-bottom: 10px solid transparent;
  border-left: 10px solid white; /* same as background */
}


/* Animate In */
.lightbox-animate-in {
  transform: scale(1);
  opacity: 1;
}

/* Close Button */
.close-button {
  position: absolute;
  top: 1rem;
  right: 1rem;
  font-size: 2rem;
  background: none;
  border: none;
  color: #333333;
  cursor: pointer;
}

/*grow gallery effect*/

.project-title-button {
