/*expandable h4*/


.expandable-button-wide {
  display: grid;
  justify-content: space-between;
  align-items: flex-start;
  border: 2px solid rgb(255, 255, 255); 
  background: none;
  padding: 0.75rem 1rem;
  cursor: pointer;
  text-align: left;
  overflow: hidden;
  font-size: 1.2em;
  color: white;
  grid-column: span 1;
  grid-row: span 2;
  /* Initial narrow width */
  width: 35%;
  transition: width 0.5s ease, background-color 0.3s ease;
}

.expandable-button-wide.open {
  width: 90%; /* expands to full width */
  grid-column: span 2;
}

.expandable-text {
  flex: 1;
  padding-right: 0.5rem;
}

.arrow {
  transition: transform 0.3s ease;
  margin-left: 0.5rem;
}

.arrow.up {
  transform: rotate(90deg); /* rotate arrow when open */
}








