/* When in mobile view */



.page-grid {
  display: flex;
  margin: 20px; /* gutter size offset */
  width: 100%;
  margin-top: 40px;
  padding-left: 5%;
  padding-right: 5%;
  grid-gap: 10px;
  flex-wrap: wrap;
}

.page-grid_column {
  padding: 20px; /* gutter size */
  background-clip: padding-box;

}

/* Style for individual items */
.page-grid_column > * {
  margin-bottom: 20px;
}


 
.page-grid img {
